.comWrapper{
    border:1px solid #eee;
}
.commentItem{
    // border:1px solid red;
    border-bottom: 1px solid #eee;
    padding:10px 20px;
    header{
        // border:1px solid red;
        height:40px;
        text-align: left;
        img{
            width:40px;
            height:40px;
            margin-right:10px;
        }
        span{
            font-size:18px;
            font-weight:600
        }
    }
    p{
        margin:10px 0 0 50px;
        text-align: left;
        font-size: 16px;
    }
}
.input{
    height:60px;
    // border:1px solid red;
    display: flex;
    padding:11px 20px 0;
    input{
        flex:1;
        height: 38px;
        border:1px solid #8590a6;
        border-radius: 5px;
        padding:10px;
        &:focus{
            outline: none;
        }
    }
    button{
        width:50px;
        height:38px;
        border:none;
        margin-left:15px;
        border-radius: 5px;
        background-color: rgba(0, 132, 255, 0.4);
        color:#fff;
        &:focus{
            outline: none;
        }
    }
}