.userWrap{
    width:1300px;
    border:1px solid #d9d9d9;
    border-radius: 10px;
    text-align: left;
    // vertical-align: center;
    .userImg{
        // width:150px;
        display:inline-block;
        margin:30px;
        // border:1px solid red;
        .avatar-uploader{
            .ant-upload{
                border-radius: 50%;
                &:hover{
                    border-color:#d9d9d9;
                }
            }
           
            img{
                width:100%;
                // height:100%;
            }
        }
    }
    .userName{
        display: inline-block;
        transform: translateY(-80px);
        font-size:20px;
        font-weight: 500;
    }
    .changeInfo{
        float: right;
        margin:60px;
    }
}