.storeContent{
    width:1400px; 
    // border:1px solid red;
   .line1{
       margin-top:20px;
       height:400px;
       text-align: left; 
       vertical-align: center;
       position: relative;
       border:1px solid #eee;
       box-shadow:1px 2px 2px #eee;
       .leftImg{
           display: inline-block;
           height: 300px;
          img{
            //   width:700px;
              height: 400px;
              z-index: -1;
              
          }
       }
       .rightText{
            // border:1px solid red;
            // margin:700px;
            display: inline-block;
            height: 300px;
            position: absolute;
            left: 940px;
            p{
               margin-top:40px;
               font-size: 34px;
               line-height: 40px;
            //    color:#000;
            }
           .exindehua{
          font-size: 18px;
          color:#999;
          padding-left: 20px;
           }
       }
   }
   .reserve{
       .leftImg{
          display: inline-block;
           height: 300px;
           position:absolute;
           left: 700px; 
       }
       .rightText{
            display: inline-block;
            height: 300px;
            position: static;
            // left: 800px;
            margin-left:100px;
       }
   }
   .line2{
       margin-top:20px;
       display:flex;
       justify-content: space-between;
       .box{
           flex:1;
           .TopImg{
            //    margin:0 5px;
               img{
                   width:95%;
               }
           }
           .BotText{
               border:1px solid #eee;
               border-top: none;
               p{
                   margin-top:10px;
                   color: #333;
                   font-size:18px;
                   font-weight: 600;
               }
           }
       }
   }
}
