.home {
  .swiper {
    width: 100%;
    img {
      width: 100%;
    }
  }
  .Ingenuity {
    width: 90%;
    margin: 100px auto;
      //  border:1px solid red;
    border-top: 1px solid #f5f5f5;
    header {
      height: 70px;
      font-size: 30px;
      font-weight: 600;
      line-height: 70px;
    }
    .content {
      height: 500px;
      // border:1px solid red;
      display: flex;
      // flex-direction: row;
      flex-wrap: nowrap;
      overflow: hidden;
      .box {
        flex: 1;
        //   border:1px  solid red;
        background-position: center;
        background-size: cover;
        span {
          margin: 200px auto;
          line-height: 80px;
          font-size: 18px;
          color: #fff;
        }
      }
      .box1 {
        background-image: url("../../images/type2.jpg");
      }
      .box2 {
        background-image: url("../../images/type4.jpg");
      }
      .box3 {
        background-image: url("../../images/type1.jpg");
      }
      .box4 {
        background-image: url("../../images/type7.jpg");
      }
      .box5 {
        background-image: url("../../images/type6.jpg");
      }
      .box6 {
        background-image: url("../../images/type5.jpg");
      }
      .box7 {
        background-image: url("../../images/type3.jpg");
      }
      .box8 {
        background-image: url("../../images/type8.jpg");
      }
      .active {
        flex: 4;
        transition-duration: 0.3s;
        // opacity: 0.5;
        span {
          display: inline-block;
          width:100%;
          font-size:26px;
          // border:1px solid red;
          background: #fff;
          color:#000;
          opacity: 0.5;
        }
      }
    }
  }
  .video {
      margin:0 auto;    
      width:70%;
    margin-top: 100px;
    // border:1px solid red;
    .vidHeader {
        margin-bottom: 10px;
      .en-title {
        font-weight: 700;
        font-size: 30px;
      }
      .ch-title {
        font-weight: 600;
        font-size: 20px;
      }
      .desc {
        font-weight: 200;
        font-size: 10px;
      }
    }
    video{
        width:100%;
    }
  }
}
