.cover{
    width:100%;
    height:100vh;
    position: fixed;
    top:0;
    // z-index: -1;
      background-color:#b8e5f8;
      background-image:url('../../images/loginBJ.jpg');

}
    .card{
     width:602px;
     height:470px;
     margin:200px auto;
     position: relative;
     background-color: #fff;
     opacity: 0.8;
    //  padding:
    border-radius: 30px;
    //  border:1px solid red;
     .title{
         width:100%;
         height:120px;
         padding-top:20px;
         color:rgb(29,80,123);
        //  border:1px solid red;
         div{
             
             height:50px;
             line-height:50px;
            //  border:1px solid red;
             display:block;
            //  width:100%;
             &:nth-of-type(1){
                 font-size: 30px;
                 font-weight: 700;
                
             }
             &:nth-of-type(2){
                font-size: 20px;
                font-weight: 400;
             }
         }
     }
     .table{
        padding:0 30px;
        .inputItem{
          height:60px;
          display: flex;
          border-bottom:1px solid #d9d9d9;
        //   border:1px solid red;
        .select{
            flex:2;
            margin:10px;
            border:none;
          .ant-select-selection{
              border:none;
          }
        }
        input{
            height:59px;
            border:none;
           &:focus{
               box-shadow: none;
             
           }
         }
        }
        .ant-btn-primary{
            background: rgb(29,80,123)
        }
        button{
            width:70%;
            height:55px;
            margin:40px 0;
            font-size: 20px;
        }
     }
     .text{
         color:#b2b2b2;
     }
     .footer{
         height:60px;
         width:100%;
         background-color: #f6f6f6;
         position: absolute;
         bottom:0;
         right:0;
         border:1px solid #ebebeb;
         line-height:60px;
          border-radius: 30px;
         span{
             cursor: pointer;
             &:nth-of-type(2){
                color:rgb(29,80,123);
             }
         }
       
     }
 }
}