.shopCar{
    .Pic{
        width: 150px;
        // border:1px solid red;
        img{
            width:100%;
        }
    }
.content{
    width:1300px;
    margin:0 auto;
    // border:1px solid  red;
   
    header{
        height:80px;
        font-size:20px;
        font-weight: 700;
        line-height: 80px;
        .goShop{
            font-size: 14px;
            float: left;
            &:hover{
             color:red;
             cursor: pointer;
            }
        }
    }
.box{
    position: relative;
}
        .table{
            width:1000px;
            p{
                height:30px;
                // border:1px solid red;
                line-height: 30px;
                font-size: 20px;
                font-weight: 600;
                color:#677807;
                // border-top:1px solid #eee;
                margin:20px 0 0;
            }
        }
        .account{
            position: absolute;
            width:250px;
            height:300px;
            top:80px;
            right:0;
            border:1px solid #eee;
          div{
              height: 60px;
              font-size:18px;
              line-height: 60px;

          }
          .jiesuan{
              width:150px;
              height: 50px;
              display: block;
              margin:20px 50px;
          }
          span{
              display: block;
          }
        }
        .exit{
            position: absolute;
            top:300px;
            right:50px;
            button{
                width:150px;
                height: 50px;
                display: block;
                // margin:20px 50px; 
            }
        }
    
  
    .ant-table-wrapper{
        width:1000px;
    }
}
}
   
   


