.header{
    width:100%;
    background:#fff;
    padding:0 25px;
    font-size:16px;
    line-height: 60px;
    display: flex;
    position: fixed;
    top:0;
    z-index: 10;
    >a{
        flex:1;
        color:#393939;
        font-weight: 600;
        // border:1px solid red;
        cursor: pointer;
        span:nth-of-type(1){
            margin-right:10px; 
        }
    }
    .exit{
        &:hover{
            color:rgb(23, 81, 153);
        }
    }
    .selected{
        // color:red;
    }
    .cartNum{
      display: inline-block;
      width:30px;
      height: 30px;
      background:#f7eae8 ;
      color:#ff5c38;
    //   opacity: 0;
      line-height: 30px;
      border-radius: 50%;
    }
    .active{
        border-bottom:2px solid rgb(23, 81, 153);
        // color:rgb(23, 81, 153);
        
    }
}