.cardItem{
   
    // width:;
    // height:150px;
    // border:1px solid #000;
    padding:20px;
    text-align:left;
    border-bottom: 1px solid #f0f2f7;
    background:#f6f6f6;;
    margin:20px 0;
    header{
      height:40px;
      color:#1a1a1a;
      font-size: 16px;
      font-weight: 600;
      &:hover{
          color:#175199;
      }
    }
      .content{
          font-size: 15px;
          line-height: 24px;
          color:rgb(26,26,26);
        .ContentItem-more {
            padding: 0;
            margin-left: 4px;
            color: #175199; 
            cursor: pointer;
        }
      }
     .other{
         font-size:20px;
         
         span{
           button{
               cursor: pointer;
               width:80px;
               height: 35px;
               border:none;
               background-color:rgba(0,132,255,0.1);
               color:#0084ff;
               border-radius: 4px;
               &:focus{
                outline: none;
              }
               &:nth-of-type(2){
                   width:30px;
                   margin-left: 5px;
               }
           }
           .like,.dislike{
            color:#fff;
            background-color:#0084ff;
            transition: all .5s;
           }
           &:nth-of-type(2){
               margin-left:50px;
               color: #8590a6;
               font-size:16px;
               cursor: pointer;
               &:hover{
                color: #76839b;
               }
           }
         }
     }
   
}
.showCom{
  display: block;
}
.hideCom{
  display: none;
}
