.rent {
  min-width: 1000px;
  width: 90%;
  margin: 0 auto;
  background-color: #f8f8f8;
  .selectList {
    padding: 0 30px;
    // border: 1px solid red;

    .line {
      height: 85px;
      text-align: left;
      padding: 26px;
      border-bottom: 1px solid #eee;
      .selItem {
        color:#ff5c38;;
        background: #f7eae8;
      }
      div {
        width: 80px;
        height: 33px;
        line-height: 33px;
        text-align: center;
        border-radius: 30px;
        background: #ffff;
        margin: 0 10px;
        // font-size: 16px;
        display: inline-block;
        color: #999;
        margin: 0 20px;
        &:hover {
          cursor: pointer;
         color:#ff5c38;;
        background: #f7eae8;
        }
      }
      .title {
        background-color: #7f7f8b;
        border: none;
        border-radius: 16px 0 16px 16px;
        color: #fff;
        height: 35px;
        width: 60px;
        margin-right: 20px;
        // font-size:16px;
      }
    }
  }
  .content {
    margin-top: 30px;
    // border: 1px solid red;
    padding: 0 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    .boxItem {
      margin: 0 20px 70px;
      position: relative;
      border: 1px solid #eee;
      transition: all 0.5s;
      background: #fff;
      border-radius: 5px;
      .card {
        // flex-basis: 250px;
        height: 340px;
        width: 220px;
        img {
          width: 100%;
          height:260px;
          // text-align: left;
          // margin-top:7px;
        }
        .info {
          // height
          width: 220px;
         
          padding: 0 13px;

          font-size: 16px;
          font-weight: 700;
          margin-top: 20px;
          .title {
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
             height: 38px;
          }
          .desc {
            font-size: 15px;
            color: #999;
            font-weight: 300;
            margin: 10px 15px;
          }
        }
      }
      .alterCard {
        visibility: hidden;
        position: absolute;
        top: 0;
        left: 0;
        button {
          position: fixed;
          bottom: 30px;
          left: 60px;
        }
      }
      &:hover .alterCard {
        transform: scale(1.1, 1.1);
        visibility: visible;
      }
    }
  }
}
.current {
  font-weight: 700;
  color: #ff5c38;
  background-color: #f7eae8;
  width: 60px;
  border-radius: 20px;
}
